.navbar {
    background-color: #101010;
    height: 3.5em;

    .button-container{
        display: flex;
        height: 100%;
        align-items: center; 
        justify-content: center;
        .title {
            transition: all 0.5s ease;
            text-align: center;
            font-size: 40px;
            @media (max-width: 720px) {
                font-size: 15px;
            }
            flex-grow: 1.5;
        }

        .button {

            -moz-user-select: -moz-none;
            -khtml-user-select: none;
            -webkit-user-select: none;
            -ms-user-select: none;
            user-select: none;
            transition: all 0.5s ease;
            font-size: 25px;  
            @media (max-width: 720px) {
                font-size: 10px;
            }             
            display: flex;
            height: 100%;
            flex-grow: 1;
            align-items: center; 
            justify-content: center;

            &:hover:not(.selected) {
            cursor: pointer;
            background-color: #D3D3D3;
            color: #080808;
            }
        }
        .selected{
            transition: all 0.25s ease;
            background-color: #F5F5F5;
            color: #080808;

            &:hover {
                cursor: default;
            }
        }
    }
}
