
.social-containers {
    display: flex;
    justify-content: center;
    align-items: center;

    .social-picture-container {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 1.5em 1em;
        .social-picture {
            border-radius: 1.5em;
            width: 5em;
            height: 5em;
            flex-grow: 1;
            &:hover {
                cursor: pointer;
                background-color: #D3D3D3;
            }

        }
}
}

.message-container {
    transition: all 0.5s ease;
    width: 45em;
    height: 35em;
    margin: 1em;
    padding: 2em;
    border-radius: 1em;
    background-color: #080808;

    div:not(.button-c) {
        padding: 0 0 .5em 0;
    }

    .input-div {
        display: flex;
        justify-content: center;
        height: 22em;
        
        .input {
            border: none;
            outline: none;
            -webkit-box-shadow: none;
            -moz-box-shadow: none;
            box-shadow: none;
            border-radius: 1em;
            font-size: 1em;
            width: 100%;
            height: 100%;
            padding: 0.25em 0.5em;
            resize: none;
        }
    }
    .m-input {
        border: none;
        outline: none;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        padding: 0.25em 0.5em;
        border-radius: .5em;
        margin: 0.5em;
    }

}


.display-flex {
    display: flex;
}

.button-container-c {
    margin: 1em;
}

.button-c {-moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: all 0.5s ease;
    font-size: 25px;  
    @media (max-width: 720px) {
        font-size: 20px;
    }           
    padding: 0;  
    display: flex;
    height: 100%;
    flex-grow: 1;
    align-items: center; 
    justify-content: center;

    &:hover:not(.selected) {
    cursor: pointer;
    background-color: #C0C0C0;
    color: #080808;
    }
    background-color: #585858;
    border-radius: .5em;
    padding : .25em
}