
.image-container {
    padding-top:2em;
    display: flex;
    align-items: center; 
    justify-content: center;
}

.image {
    border-radius: 50%;
    padding:0.3em;
    border-style: solid;
    border-width: .3em;
    width: 25em;
    height: 25em;
}

.container {
    transition: all 0.5s ease;
    display: flex;
    align-items: center; 
    justify-content: center;
    
    .important {
    padding-top: .5em;
        text-align: center;
        font-size: 30px;  
        @media (max-width: 720px) {
            font-size: 15px;
        }   
    }
    
    .text{
        width: 50%;
        text-align: center;
    }

    .button {

        -moz-user-select: -moz-none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
        transition: all 0.5s ease;
        font-size: 23px;  
        background-color: #101010;
        padding: 0.3em;
        border-radius: 0.5em;
        @media (max-width: 720px) {
            font-size: 18px;
        }             
        text-align: center;
    
        &:hover {
            cursor: pointer;
            background-color: #D3D3D3;
            color: #080808;
        }
    }

}
