
.page-container {
    padding: 1em;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.project-container {
    transition: all 0.5s ease;
    width: 15em;
    height: 15em;
    margin: 1em;
    padding: 2em;
    border-radius: 1em;
    background-color: #080808;

    h3 {
        margin-top: 0;
    }

    &:hover {
        cursor: pointer;
        background-color: #D3D3D3;
        color: #080808;
    }

}

.description {
    overflow: auto;
    height: 80%
}

/* width */
::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }