
.first-level {
    transition: all 0.5s ease;
    border: solid;
    border-width: 4px 0 0 0;
    border-color: #404040;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    .button-container {
        width:25em;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .button {
        margin: 1em 0px;
        -moz-user-select: -moz-none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
        transition: all 0.5s ease;
        border-radius: 0.5em;
        padding: 0.75em;
        background-color: #000;
        float: right;

        &:hover {
            cursor: pointer;
            background-color: #D3D3D3;
            color: #080808;
        }
    }
    
    h1{
        margin: 0px;
        line-height: 1em;
    }

}

.second-level {
    border: solid;
    color: #484848;
    border-width: 2px 0 0 0;
    transition: all 0.5s ease;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: right;

    .second-level-button { 
        color: #FFF;
        -moz-user-select: -moz-none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
        transition: all 0.5s ease;
        min-width: 3em;
        border-radius: 0.5em;
        padding: 0.5em 0.75em;
        background-color: #000;
        float: right;

        &:hover {
            cursor: pointer;
            background-color: #D3D3D3;
            color: #080808;
        }
    }

    .second-level-button-container {
        width: 40%;
        margin: .5em 0px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    h2 {
        text-align: center;
        margin: 0px;
        line-height: 1em;
    }

    h1{
        margin: 0px;
        line-height: 1em;
    }

    
    div:not(.second-level-button):not(.second-level-button-container) {
        float: right;
        flex-grow: 1;
        @media (max-width: 720px) {       
        padding-left: 1em;
        }   
        div{      
            text-align: start;
        }
    }
}

.second-column {
    width: 100%;
    text-align: end;
    float: right;

    .no-border {
        border-width: 0;
    }
}

.zone-padding {
    
    color: #FFF;
    width: 50%;
    padding: 1em 0;
    flex-grow: 1;
}

.third-level {
    width: 80%;
          
    float: left !important;
    padding: .3em 0;
    @media (max-width: 720px) {       
        padding: .3em;
        }  
}

.ec-container {
    display: flex;

    .ec-button-container {
        padding: 3em 0 1em 1em;

        .ec-button {
            color: #FFF;
            -moz-user-select: -moz-none;
            -khtml-user-select: none;
            -webkit-user-select: none;
            -ms-user-select: none;
            user-select: none;
            transition: all 0.5s ease;
            min-width: 3em;
            border-radius: 0.5em;
            padding: 0.5em;
            background-color: #000;
            float: right;
            &:hover {
                cursor: pointer;
                background-color: #D3D3D3;
                color: #080808;
            }
        }

    }
}

.rp-text {
    font-size: 1.2em;
    padding-right: 0.4em;
}